import React from "react";
import "./App.css";
import { vaccineAvailabilityCenters } from "./sampleData.js";

const HomePageHeader = () => {
    return (
      <header className="header">
        <h2>Your Stock Tracker</h2>
      </header>
    );
  };

  const VaccineAvailabilityCenter = ({ centerName, centerAddress, centerBlock, centerPinCode, centerFeeType }) => {
    if (!centerName) return <div />;
    return (
      <table>
        <tbody>
          <tr>
            <td>
              <h5>{centerName}</h5>
            </td>
            <td>
              <h5>{centerAddress}</h5>
            </td>
            <td>
              <h4>{centerBlock}</h4>
            </td>
            <td>
              <p>{centerPinCode}</p>
            </td>
            <td>
              <p>{centerFeeType}</p>
            </td>            
          </tr>
        </tbody>
      </table>
    );
  };

export const VaccineAvailabilityData = () => {
  return (
    <>
       <HomePageHeader />
       <div>

      {vaccineAvailabilityCenters.map((data, key) => {
          return (
            <div key={key}>
            <VaccineAvailabilityCenter 
            key={key}
            centerName = {data.name}
            centerAddress = {data.address}
            centerBlock = {data.block_name}
            centerPinCode = {data.pincode}
            centerFeeType = {data.fee_type}
            />
            </div>
          );
        })}

      </div>
    </>
  );
};